<template>
  <div class="withdraw">
    <div class="input-panel">
      <div class="input-title">Note</div>
      <a-input v-model="privateNote" @change="loadNoteData">
      </a-input>
    </div>
    <div class="input-panel">
      <div class="input-title">
        Recipient
      </div>
      <a-input v-model="recipient">
      </a-input>
    </div>
    <!-- <div class="checkbox-panel">
    TODO: add relayer
      <a-checkbox v-model="useRelayer"> Use secure relayer </a-checkbox>
      <a-tooltip title="Prompt Text">
        <img src="../assets/tips.svg" />
      </a-tooltip>
    </div> -->

    <div v-if="!user" class="button-panel" >
      <a-button class="main-btn" @click="connectWallet"> CONNECT </a-button>
    </div>
    <div v-else class="button-panel" >
      <a-button class="main-btn-half" @click="withdraw" :disabled="!privateNote || !recipient"> WITHDRAW </a-button>
      <a-button class="main-btn-half" @click="loadPrivateNoteStats" :disabled="!privateNote"> NOTE STATS </a-button>
    </div>

    <a-modal v-if="privateNoteStats" v-model="showPrivateNoteStats" title="Private Note Stats" dialogClass="account-modal" :footer="null">
      <ul class="account-panel">
        <li>
          <div class="main-data">
            <div class="title">Amount</div>
            <div class="data">{{privateNoteStats['mixer']['denomination'] | formatNumberInKilo(1000) }} {{privateNoteStats['mixer']['token']}}</div>
          </div>
        </li>
        <li>
          <div class="main-data">
            <div class="title">Status</div>
            <div class="data">{{privateNoteStats['isSpent']? "Spent": "In Pool"}}</div>
          </div>
        </li>
        <li>
          <div class="main-data">
            <div class="title">Subsequent Deposits</div>
            <div class="data">{{ privateNoteStats['subsequentDeposits'] }}</div>
          </div>
        </li>
        <li>
          <div class="main-data">
            <div class="title">Time Since Deposit</div>
            <div class="data">{{ privateNoteStats['timeSinceDeposit'] }}</div>
          </div>
        </li>
      </ul>
    </a-modal>
  </div>
</template>
<script>
import "./Withdraw.scss";
import { mapState } from "vuex"
import * as contractUtils from '@/common/utils/contractUtils.js'
import ADDRESS from '@/common/address/address.js'
import CHAIN from '@/common/chain/chain.js'
import * as MixerCore from '@/common/core/mixer.js'

export default {
  data() {
    return {
      // TODO: load note data and validate address?
      privateNote: null,
      recipient: null,
      showPrivateNoteStats: false,
      privateNoteStats: null
    }
  },

  computed: {
    ...mapState({
      user: state => state.wallet.user,
      chainId: state => state.wallet.chainId,
      isWeb3Initialized: state => state.wallet.isWeb3Initialized,
      isDBInitialized: state => state.onionDB.isDBInitialized,
      circuit: state => state.onionDB.circuit,
      provingKey: state => state.onionDB.provingKey,
      cachedDepositEventsForAllMixers: state => state.onionDB.cachedDepositEventsForAllMixers
    })
  },

  methods: {
    loadNoteData() {
      // TODO: add load note data?
      console.log(this.privateNote)
    },

    async loadPrivateNoteStats() {
      const loading = this.$loading({ lock: true, text: 'loading note stats...', background: 'rgba(0, 0, 0, 0.7)'})

      try {
        // TODO: don't wait for receipt (migh take too long in ethereum chain) but wait for txHash
        this.privateNoteStats = await MixerCore.loadPrivateNoteStats(window.web3, this.privateNote, this.cachedDepositEventsForAllMixers)
        this.showPrivateNoteStats = true
      } catch (e) {
        console.error(e)
        this.$notification.error({ message: e.message})
      } finally {
        loading.close()
      }
    },

    async withdraw() {
      console.log('withdraw')
      const loading = this.$loading({ lock: true, text: 'withdrawing...', background: 'rgba(0, 0, 0, 0.7)'})

      try {
        // TODO: don't wait for receipt (migh take too long in ethereum chain) but wait for txHash
        const receipt = await MixerCore.withdraw(window.web3, this.privateNote, this.recipient, this.circuit, this.provingKey, this.cachedDepositEventsForAllMixers)
        this.openTxNotification('Withdraw Success', receipt['transactionHash'])
      } catch (e) {
        console.error(e)
        this.$notification.error({ message: e.message })
      } finally {
        loading.close()
      }
    },

    async connectWallet() {
      // TODO: add wallet logo and pop up for use to choose wallet type
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.7)"
      })
      try {
        await this.$store.dispatch('connectWallet')
      } catch (e) {
        console.error(e)
      } finally {
        loading.close()
      }
    },

    openTxNotification(message, txHash) {
      const linkHref = `${CHAIN[this.chainId]['scanLink']}/tx/${txHash}`
      const linkText = `View on ${CHAIN[this.chainId]['scanName']}`
      this.$notification.success({
        message: message,
        description: (
          <a href={linkHref} target="_blank">
            {linkText}
          </a>
        )
      })
    }
  },
};
</script>
