<template>
  <div class="deposit" v-if="chainId">
    <div class="input-panel">
      <div class="input-title">Token</div>
      <a-select v-model="selectedMixerTokenIndex" @change="changeMixerToken" size="large" style="width: 100%">
        <a-select-option v-for="(token, index) in supportedMixerTokens" :value="index" :key="token" > {{ token }} </a-select-option>
      </a-select>
    </div>

    <div class="input-panel">
      <div class="input-title">Denomination</div>
      <a-radio-group v-model="selectedMixerDenominationIndex" @change="changeMixerDenomination" button-style="solid" >
        <a-radio-button v-for="(denomination, index) in supportedMixerDenominations" :value="index"  :key="denomination">
          {{ denomination | formatNumberInKilo(1000) }}
        </a-radio-button>
      </a-radio-group>
    </div>

    <div class="tips">
      <div>Current liquidity fee {{ liquidityFee | formatPercent(2) }}%</div>
      <a-tooltip title="fee increases by 0.1% every week until reaching a cap of 0.5%">
        <div class="tips-icon"><img src="../assets/tips.svg" /></div>
      </a-tooltip>
    </div>

    <a-button v-if="!user" class="main-btn" @click="connectWallet"> CONNECT </a-button>
    <a-button v-else-if="!hasAllowance" class="main-btn" @click="approve"> APPROVE </a-button>
    <a-button v-else class="main-btn" @click="generatePrivateNote"> DEPOSIT </a-button>

    <a-modal v-model="showPrivateNoteConfirmation" title="Save Your Private Note" :footer="null">
      <!-- TODO: make sure copy button works in iphone-->
      <a-button class="copy-btn" v-clipboard="privateNote"> Copy </a-button>
      <div class="note">
        {{ privateNote }}
      </div>
      <a-checkbox v-model="privateNoteChecked">
        I have saved my private note and understand I will not be able to
        withdraw without it
      </a-checkbox>
      <a-button class="deposit-btn" @click="deposit" :disabled="!privateNoteChecked"
        >Confirm Deposit
      </a-button>
    </a-modal>
  </div>
</template>
<script>
import "./Deposit.scss"
import { mapState } from "vuex"
import * as contractUtils from '@/common/utils/contractUtils.js'
import ADDRESS from '@/common/address/address.js'
import CHAIN from '@/common/chain/chain.js'
import * as MixerCore from '@/common/core/mixer.js'
const download = require('downloadjs')

export default {
  data() {
    return {
      selectedMixerTokenIndex: 0,
      selectedMixerDenominationIndex: 0,
      liquidityFee: null,
      hasAllowance: true,  // default to true because selectedMixerTokenIndex is default to 0, which should be native token
      depositObject: null,
      privateNote: null,
      showPrivateNoteConfirmation: false,
      privateNoteChecked: false
    };
  },

  computed: {
    ...mapState({
      user: state => state.wallet.user,
      chainId: state => state.wallet.chainId,
      isWeb3Initialized: state => state.wallet.isWeb3Initialized,
      isDBInitialized: state => state.onionDB.isDBInitialized
    }),

    mixer() {
      return {
        'token': this.supportedMixerTokens[this.selectedMixerTokenIndex],
        'denomination': this.supportedMixerDenominations[this.selectedMixerDenominationIndex]
      }
    },

    supportedMixerTokens() {
      return contractUtils.getSupportedMixerTokens(this.chainId)
    },

    supportedMixerDenominations() {
      return contractUtils.getSupportedMixerDenominations(this.chainId, this.supportedMixerTokens[this.selectedMixerTokenIndex])
    }
  },

  methods: {
    async changeMixerToken() {
      // reset denomination value to the first value in supportedMixerDenominations array whenever mixer token changes
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.7)"
      })
      try {
        this.selectedMixerDenominationIndex = 0
        this.$emit("mixerChanged", this.mixer)
        await this.updateAllowance()
        this.liquidityFee = await MixerCore.getProtocolFee(window.web3, this.mixer)
      } catch (e) {
        console.error(e)
        this.$notification.error({ message: e.message })
      } finally {
        loading.close()
      }
    },

    async changeMixerDenomination() {
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.7)"
      })
      try {
        this.$emit("mixerChanged", this.mixer)
        await this.updateAllowance()
        this.liquidityFee = await MixerCore.getProtocolFee(window.web3, this.mixer)
      } catch (e) {
        console.error(e)
        this.$notification.error({ message: e.message })
      } finally {
        loading.close()
      }
    },

    async updateAllowance() {
      // only check if user is connected
      if (this.user) {
        const mixerAddress = ADDRESS[this.chainId]['Mixer'][this.mixer['token']][this.mixer['denomination']]
        if (contractUtils.isNativeToken(this.mixer['token'])) {
          this.hasAllowance = true
        } else {
          const allowance = contractUtils.convertToBaseUnit(this.chainId, this.mixer['token'], this.mixer['denomination'])
          this.hasAllowance = await contractUtils.hasAllowance(this.chainId, this.mixer['token'], this.user['account'], mixerAddress, allowance)
        }
      }
    },

    async approve() {
      console.log('approve')
      const loading = this.$loading({ lock: true, text: 'approving...', background: 'rgba(0, 0, 0, 0.7)'})
      const mixerAddress = ADDRESS[this.chainId]['Mixer'][this.mixer['token']][this.mixer['denomination']]
      try {
        await contractUtils.approve(this.chainId, this.mixer['token'], this.user['account'], mixerAddress)
        await this.updateAllowance()
      } catch (e) {
        console.error(e)
        this.$notification.error({ message: e.message })
        this.$notification.error({ message: 'Failed to approve, please refresh page and retry.'})
      } finally {
        loading.close()
      }
    },

    async generatePrivateNote() {
      console.log('generatePrivateNote')
      const loading = this.$loading({ lock: true, text: 'generating private note...', background: 'rgba(0, 0, 0, 0.7)'})
      const mixerAddress = ADDRESS[this.chainId]['Mixer'][this.mixer['token']][this.mixer['denomination']]
      // generating private note and deposit object
      try {
        const {depositObject, privateNote} = await MixerCore.prepareForDeposit(window.web3, this.mixer)
        this.depositObject = depositObject
        this.privateNote = privateNote
        this.showPrivateNoteConfirmation = true
        console.log(`depositObject=${depositObject}, privateNote=${privateNote}`)
      } catch (e) {
        this.resetDepositState()
        console.error(e)
        this.$notification.error({ message: e.message })
        this.$notification.error({ message: 'Failed to generate private note, please refresh page and retry.'})
      } finally {
        loading.close()
      }
    },

    async deposit() {
      console.log('deposit')
      const loading = this.$loading({ lock: true, text: 'depositing...', background: 'rgba(0, 0, 0, 0.7)'})

      try {
        download(this.privateNote, "privateNote.txt", "text/plain")
        // TODO: don't wait for receipt (migh take too long in ethereum chain) but wait for txHas
        const receipt = await MixerCore.deposit(window.web3, this.mixer, this.depositObject)
        console.log(receipt)
        this.openTxNotification('Deposit Success', receipt['transactionHash'])
      } catch (e) {
        console.error(e)
        this.$notification.error({ message: e.message })
        this.$notification.error({ message: 'Failed to deposit, please refresh page and retry.'})
      } finally {
        this.resetDepositState()
        loading.close()
      }
    },

    // clear and reset all deposit stateful variables
    resetDepositState() {
      this.depositObject = null,
      this.privateNote = null,
      this.showPrivateNoteConfirmation = false,
      this.privateNoteChecked = false
    },

    async connectWallet() {
      // TODO: add wallet logo and pop up for use to choose wallet type
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.7)"
      })
      try {
        await this.$store.dispatch('connectWallet')
      } catch (e) {
        console.error(e)
        this.$notification.error({ message: e.message })
      } finally {
        loading.close()
      }
    },

    openTxNotification(message, txHash) {
      const linkHref = `${CHAIN[this.chainId]['scanLink']}/tx/${txHash}`
      const linkText = `View on ${CHAIN[this.chainId]['scanName']}`
      this.$notification.success({
        message: message,
        description: (
          <a href={linkHref} target="_blank">
            {linkText}
          </a>
        )
      })
    }
  },

  mounted() {
  },

  async created() {
    console.log('created deposit')
    const loading = this.$loading({
      lock: true,
      background: "rgba(0, 0, 0, 0.7)"
    })
    try {
      await this.$store.dispatch('initWeb3') // wait initWeb3 to make sure chainId and web3 (if any) is set
      if (this.isWeb3Initialized) {
        this.liquidityFee = await MixerCore.getProtocolFee(window.web3, this.mixer)
      }
      this.$emit("mixerChanged", this.mixer)
    } catch (e) {
      console.error(e)
      this.$notification.error({ message: e.message })
    } finally {
      loading.close()
    }
  }
};
</script>
