const JSZip = require('jszip')
const axios = require('axios')

export default {
  state: {
    isDBInitialized: false,
    circuit: null,
    provingKey: null,
    cachedDepositEventsForAllMixers: null
  },

  getters: {
    isDBInitialized: state => state.isDBInitialized,
    circuit: state => state.circuit,
    provingKey: state => state.provingKey,
    cachedDepositEventsForAllMixers: state => state.cachedDepositEventsForAllMixers
  },

  actions: {
    async initDB({state, commit, rootState, rootGetters, dispatch}) {
      const [response1, response2] = await Promise.all([
        axios.get('https://raw.githubusercontent.com/Onion-Mixer/onion-db/main/events/depositEvents.json.zip',  {responseType: 'arraybuffer'}),
        axios.get('https://raw.githubusercontent.com/Onion-Mixer/onion-db/main/zksnarks/withdraw.zip', {responseType: 'arraybuffer'})
      ])
      var jsZip = new JSZip()
      const withdrawZip = await jsZip.loadAsync(response1.data)
      const depositEventsZip = await jsZip.loadAsync(response2.data)

      const circuit = JSON.parse(await withdrawZip.file("withdraw.json").async("string"))
      const provingKey = await withdrawZip.file("withdraw_proving_key.bin").async("arraybuffer")
      const cachedDepositEventsForAllMixers = JSON.parse(await depositEventsZip.file("depositEvents.json").async("string"))

      commit('setCircuit', circuit)
      commit('setProvingKey', provingKey)
      commit('setCachedDepositEventsForAllMixers', cachedDepositEventsForAllMixers)
      commit('setIsDBInitialized', true)
    },

  },

  mutations: {
    setIsDBInitialized(state, isDBInitialized) {
      state.isDBInitialized = isDBInitialized
    },

    setCircuit(state, circuit) {
      state.circuit = circuit
    },

    setProvingKey(state, provingKey) {
      state.provingKey = provingKey
    },

    setCachedDepositEventsForAllMixers(state, cachedDepositEventsForAllMixers) {
      state.cachedDepositEventsForAllMixers = cachedDepositEventsForAllMixers
    }
  }
}
