
export function getCurrentTimeInSeconds() {
  const currentTimeInMs= new Date().getTime()
  const currentTimeInSeconds = (currentTimeInMs - (currentTimeInMs % 1000)) / 1000

  return currentTimeInSeconds
}

export function ssToHHMMSS(durationInSeconds) {
  if (durationInSeconds <=0) {
    return '00:00:00'
  }

  var secs = durationInSeconds % 60
  durationInSeconds = (durationInSeconds - secs) / 60
  var mins = durationInSeconds % 60
  var hrs = (durationInSeconds - mins) / 60

  return `${hrs} hrs ${mins} mins ${secs} secs`
}
