<script>
import "./App.scss"
import "./Media.scss"
import { mapState } from "vuex"
import CHAIN from '@/common/chain/chain.js'

export default {
  data() {
    return {
      visible: false,
      supportedChains: [97],
      isAppInitialized: false,
    }
  },

  computed: {
    ...mapState({
      user: state => state.wallet.user,
      chainId: state => state.wallet.chainId,
      isWeb3Initialized: state => state.wallet.isWeb3Initialized,
      isDBInitialized: state => state.onionDB.isDBInitialized
    }),

    isSupportedChain() {
      return this.supportedChains.includes(Number(this.chainId))
    }

  },

  methods: {
    async connectWallet() {
      // TODO: add wallet logo and pop up for use to choose wallet type
      // TODO: account summary - entry point not obvious
      const loading = this.$loading({
        lock: true,
        background: "rgba(0, 0, 0, 0.7)"
      })
      try {
        await this.$store.dispatch('connectWallet')
      } catch (e) {
        console.error(e)
      } finally {
        loading.close()
      }
    },

    showModal() {
      this.visible = true;
    }
  },

  async created() {
    const loading = this.$loading({
      lock: true,
      background: "rgba(0, 0, 0, 0.7)",
      text: 'syncing deposit events...'
    })
    try {
      await this.$store.dispatch('initWeb3')
      await this.$store.dispatch('initDB')
    } catch (e) {
      console.error(e)
      this.$notification.error({ message: e.message })
    } finally {
      loading.close()
    }
    this.isAppInitialized = true
    if (!this.isSupportedChain) {
      const supportedChainNames = []
      for (const chainId of this.supportedChains) {
        supportedChainNames.push(CHAIN[chainId]['name'])
      }
      this.$notification.error({ message: `Current network not supported, Please switch to one of the following: ${supportedChainNames}`, duration: 0})
    }
  }
}
</script>
