import ADDRESS from '@/common/address/address.js'
import ABI from '@/common/abi/abi.js'
import * as contractUtils from '@/common/utils/contractUtils.js'
const Web3 = require('web3')
import { initWeb3MetaMask } from "@/common/utils/web3/initWeb3MetaMask.js"

export default {
  state: {
    isWeb3Initialized: false, // can be either default web3 or user connected web3
    chainId: null, // default to BSC mainnet
    user: null // user will be null if not connected to wallet (e.g metamask)
  },

  getters: {
    isWeb3Initialized: state => state.isWeb3Initialized,
    chainId: state => state.chainId,
    user: state => state.user
  },

  actions: {
    async initWeb3({state, commit, rootState, rootGetters, dispatch}) {
      if (!state.isWeb3Initialized) {
        console.log('initWeb3')
        if (Web3.givenProvider) {
          window.web3 = new Web3(Web3.givenProvider)
        } else {
          console.log('TODO: use common account for loading public info')
          //window.web3 = new Web3(new Web3.providers.HttpProvider(INFURA_ENDPOINT[1]))
          // TODO: default to BSC chain
          commit('setChainId', 56)
        }
        // TODO: now here will throw exception if there is no wallet in browser
        commit('setChainId', await window.web3.eth.getChainId())
        await dispatch('_subscribeToWalletChanges')
        commit('setIsWeb3Initialized', true)
      }
      dispatch('loadUserAccountDetails')
    },

    async connectWallet({state, commit, rootState, rootGetters, dispatch}, walletName) {
      let connected = false
      switch (walletName) {
        case 'MetaMask':
          connected = await initWeb3MetaMask()
          break
        default:
          connected = await initWeb3MetaMask()
      }
      if (connected) {
        commit('setChainId', await window.web3.eth.getChainId())
        await dispatch('_subscribeToWalletChanges')
        commit('setIsWeb3Initialized', true)
        dispatch('loadUserAccountDetails')
      } else {
        throw 'Failed to connect to wallet, please make sure wallet is installed!'
      }
    },

    async loadUserAccountDetails({state, commit, rootState, rootGetters, dispatch}) {
      console.log('loadUserAccountDetails')
      if (state.isWeb3Initialized) {
        let accounts = await window.web3.eth.getAccounts()
        if (accounts && accounts[0]) {
          let user = {
            'balances': {},
            'account': accounts[0]
          }
          // tokens should include our token and all supportedMixerTokens
          let tokens = contractUtils.getSupportedMixerTokens(state.chainId)
          if (!tokens.includes(contractUtils.getOurToken())) {
            tokens.push(contractUtils.getOurToken())
          }
          for (const token of tokens) {
            let balanceMaxUnit
            if (!contractUtils.isNativeToken(token)) {
              const tokenContract = new window.web3.eth.Contract(ABI['ERC20'], ADDRESS[state.chainId][token])
              balanceMaxUnit = contractUtils.convertToMaxUnit(state.chainId, token, await tokenContract.methods.balanceOf(user['account']).call())
            } else {
              balanceMaxUnit = contractUtils.convertToMaxUnit(state.chainId, token, await window.web3.eth.getBalance(user['account']))
            }
            user['balances'][token] = balanceMaxUnit
          }
          console.log(`user=${JSON.stringify(user, null, 4)}`)
          commit('setUser', user)
        }
      }
    },

    _subscribeToWalletChanges({state, commit, rootState, rootGetters, dispatch}) {
      let provider = window.web3.currentProvider

      provider.on("accountsChanged", (accounts) => {
        window.location.reload(false)
      })

      provider.on("chainChanged", (chainId) => {
        window.location.reload(false)
      })

      provider.on("networkChanged", (networkId) => {
        window.location.reload(false)
      })
    }

  },

  mutations: {
    setUser(state, user) {
      state.user = user
    },

    setChainId(state, chainId) {
      state.chainId = chainId
    },

    setIsWeb3Initialized(state, isWeb3Initialized) {
      state.isWeb3Initialized = isWeb3Initialized
    }
  }
}
