import ADDRESS from '@/common/address/address.js'
import ABI from '@/common/abi/abi.js'
import * as contractUtils from '@/common/utils/contractUtils.js'
import * as commonUtils from '@/common/utils/commonUtils.js'
import MIXER_CONFIG from '@/common/config/mixerConfig.js'

const snarkjs = require('snarkjs')
const crypto = require('crypto')
const circomlib = require('circomlib')
const websnarkUtils = require('websnark/src/utils')
const merkleTree = require('../zksnarks/MerkleTree')
const buildGroth16 = require('websnark/src/groth16')

// ******************** for deposit ******************** //

/* create deposit object and private note to user as a preparation for deposit */
export async function prepareForDeposit(web3, mixer) {
  const chainId = await web3.eth.net.getId()
  console.log(`prepareForDeposit: chainId=${chainId}, mixer=${JSON.stringify(mixer)}`)

  // Generate random number of specified byte length
  const rbigint = nbytes => snarkjs.bigInt.leBuff2int(crypto.randomBytes(nbytes))
  const depositObject = _createDepositObject(rbigint(31), rbigint(31))
  const secret = _toHex(depositObject.preimage, 62)
  const privateNote = `onion-${mixer['token']}-${mixer['denomination']}-${chainId}-${secret}`
  return {depositObject, privateNote}
}

/* make deposit */
export async function deposit(web3, mixer, depositObject) {
  const [chainId, accounts] = await Promise.all([ web3.eth.net.getId(), web3.eth.getAccounts()])
  console.log(`deposit: chainId=${chainId}, mixer=${JSON.stringify(mixer)}`)

  const mixerContract = new web3.eth.Contract(ABI['Mixer'], ADDRESS[chainId]['Mixer'][mixer['token']][mixer['denomination']])
  let receipt;
  if (contractUtils.isNativeToken(mixer['token'])) {
    const denominationInBaseUnit = contractUtils.convertToBaseUnit(chainId, mixer['token'], mixer['denomination'])
    receipt = await mixerContract.methods.deposit(_toHex(depositObject.commitment)).send({ value: denominationInBaseUnit, from: accounts[0]})
  } else {
    receipt = await mixerContract.methods.deposit(_toHex(depositObject.commitment)).send({ from: accounts[0] })
  }

  return receipt
}

/**
 * Create deposit object from secret and nullifier
 */
function _createDepositObject(nullifier, secret) {
  const pedersenHash = data => circomlib.babyJub.unpackPoint(circomlib.pedersenHash.hash(data))[0]
  const depositObject = { nullifier, secret }
  depositObject.preimage = Buffer.concat([depositObject.nullifier.leInt2Buff(31), depositObject.secret.leInt2Buff(31)])
  depositObject.commitment = pedersenHash(depositObject.preimage)
  depositObject.commitmentHex = _toHex(depositObject.commitment)
  depositObject.nullifierHash = pedersenHash(depositObject.nullifier.leInt2Buff(31))
  depositObject.nullifierHex = _toHex(depositObject.nullifierHash)
  return depositObject
}

/** BigNumber to hex string of specified length */
function _toHex(number, length = 32) {
  const str = number instanceof Buffer ? number.toString('hex') : (snarkjs.bigInt)(number).toString(16)
  return '0x' + str.padStart(length * 2, '0')
}


// ******************** for withdraw ******************** //

export async function withdraw(web3, privateNote, recipient, circuit, provingKey, cachedDepositEventsForAllMixers) {
  const [chainId, accounts, latestBlockNumber] = await Promise.all([ web3.eth.net.getId(), web3.eth.getAccounts(), web3.eth.getBlockNumber()])
  const { mixer, privateNoteChainId, depositObject } = _parsePrivateNote(privateNote)
  if (chainId != privateNoteChainId) {
    throw new Error('The private note is for a different network, please switch network and retry.')
  }
  const mixerContract = new web3.eth.Contract(ABI['Mixer'], ADDRESS[chainId]['Mixer'][mixer['token']][mixer['denomination']])
  // generate Merkle Proof
  const cachedDepositEvents = cachedDepositEventsForAllMixers[chainId][mixer['token']][mixer['denomination']]['events']
  const cachedUntilBlockNumber = cachedDepositEventsForAllMixers[chainId][mixer['token']][mixer['denomination']]['toBlock']
  const newDepositEvents = await contractUtils.getDepositEvents(web3, chainId, mixer, cachedUntilBlockNumber + 1, latestBlockNumber)
  const depositEvents = cachedDepositEvents.concat(newDepositEvents)
  console.log(cachedDepositEvents)
  console.log(newDepositEvents)
  console.log(depositEvents)
  const merkleTreeHeight = MIXER_CONFIG[chainId][mixer['token']][mixer['denomination']]['merkleTreeHeight']
  const merkleProof = await _generateMerkleProof(mixerContract, merkleTreeHeight, depositObject, depositEvents)
  // generate proof
  const { proof, args } = await _generateProof(depositObject, recipient, circuit, provingKey, merkleProof)
  // withdraw
  console.log('Submitting withdraw transaction')
  const receipt = await mixerContract.methods.withdraw(proof, ...args).send({ from: accounts[0] })
  console.log('Done')
  return receipt
}


function _parsePrivateNote(privateNote) {
  const match = _matchPrivateNote(privateNote)
  if (!match) {
    throw new Error('The private note has invalid format')
  }
  const buf = Buffer.from(match.groups.note, 'hex')
  const depositObject = _createDepositObject(snarkjs.bigInt.leBuff2int(buf.slice(0, 31)), snarkjs.bigInt.leBuff2int(buf.slice(31, 62)))
  const mixer = {
    'token': match.groups.token,
    'denomination': match.groups.denomination
  }
  return { mixer, 'privateNoteChainId': match.groups.chainId, depositObject }
}

function _matchPrivateNote(privateNote) {
  const noteRegex = /onion-(?<token>\w+)-(?<denomination>[\d.]+)-(?<chainId>\d+)-0x(?<note>[0-9a-fA-F]{124})/g
  const match = noteRegex.exec(privateNote)

  return match
}

/**
 * Generate SNARK proof for withdrawal
 * @param depositObject Deposit object
 * @param recipient Funds recipient
 * @param relayer Relayer address
 * @param fee Relayer fee
 * @param refund Receive ether for exchanged tokens
 */
async function _generateProof(depositObject, recipient, circuit, provingKey, merkleProof, relayerAddress = 0, fee = 0, refund = 0) {
  // Prepare circuit input
  const input = {
    // Public snark inputs
    root: merkleProof.root,
    nullifierHash: depositObject.nullifierHash,
    recipient: (snarkjs.bigInt)(recipient),
    relayer: (snarkjs.bigInt)(relayerAddress),
    fee: (snarkjs.bigInt)(fee),
    refund: (snarkjs.bigInt)(refund),

    // Private snark inputs
    nullifier: depositObject.nullifier,
    secret: depositObject.secret,
    pathElements: merkleProof.path_elements,
    pathIndices: merkleProof.path_index,
  }
  console.time('buildGroth16')
  const groth16 = await buildGroth16()
  console.timeEnd('buildGroth16')
  console.time('Generating SNARK proof')
  const proofData = await websnarkUtils.genWitnessAndProve(groth16, input, circuit, provingKey)
  const { proof } = websnarkUtils.toSolidityInput(proofData)

  const args = [
    _toHex(input.root),
    _toHex(input.nullifierHash),
    _toHex(input.recipient, 20),
    _toHex(input.relayer, 20),
    _toHex(input.fee),
    _toHex(input.refund)
  ]
  console.timeEnd('Generating SNARK proof')

  return { proof, args }
}

/**
 * Generate merkle tree for a deposit.
 * Download deposit events from the mixer, reconstructs merkle tree, finds our deposit leaf
 * in it and generates merkle proof
 */
async function _generateMerkleProof(mixerContract, merkleTreeHeight, depositObject, depositEvents) {
  console.time('_generateMerkleProof')
  const leaves = depositEvents
    .sort((a, b) => a.leafIndex - b.leafIndex)
    .map(e => e.commitment);
  const tree = new merkleTree(merkleTreeHeight, leaves) // 15k leaves takes about 25 seconds to construct the tree
  // Find current commitment in the tree
  const depositEvent = depositEvents.find(e => e.commitment === _toHex(depositObject.commitment))
  const leafIndex = depositEvent ? depositEvent.leafIndex : -1

  // Validate that our data is correct
  const root = await tree.root()
  const isValidRoot = await mixerContract.methods.isKnownRoot(_toHex(root)).call()
  const isSpent = await mixerContract.methods.isSpent(_toHex(depositObject.nullifierHash)).call()

  if (!isValidRoot) {
    throw new Error('Merkle tree is corrupted')
  }
  if (isSpent) {
    throw new Error('The private note is already spent')
  }
  if (leafIndex < 0) {
    throw new Error('The deposit is not found in the tree')
  }
  console.timeEnd('_generateMerkleProof')
  // Compute merkle proof of our commitment
  return tree.path(leafIndex)
}

// ******************** load note stats ******************** //

export async function loadPrivateNoteStats(web3, privateNote, cachedDepositEventsForAllMixers) {
  const [chainId, accounts, latestBlockNumber] = await Promise.all([ web3.eth.net.getId(), web3.eth.getAccounts(), web3.eth.getBlockNumber()])
  const { mixer, privateNoteChainId, depositObject } = _parsePrivateNote(privateNote)
  if (chainId != privateNoteChainId) {
    throw new Error('The private note is for a different network, please switch network and retry.')
  }
  // try to find event in cached event first
  let depositEvent = cachedDepositEventsForAllMixers[chainId][mixer['token']][mixer['denomination']]['events'].find(e => e.commitment === depositObject.commitmentHex)
  // try to find in latest events if not found in cache
  if (!depositEvent) {
    const cachedUntilBlockNumber = cachedDepositEventsForAllMixers[chainId][mixer['token']][mixer['denomination']]['toBlock']
    const events = await contractUtils.getDepositEvents(web3, chainId, mixer, cachedUntilBlockNumber, latestBlockNumber, depositObject.commitmentHex)
    if (events.length == 0) {
      throw new Error('The private note does not exist in our system')
    } else if (events.length > 1) {
      throw new Error('More than one deposit matched in our system')
    } else {
      depositEvent = events[0]
    }
  }

  const mixerContract = new web3.eth.Contract(ABI['Mixer'], ADDRESS[chainId]['Mixer'][mixer['token']][mixer['denomination']])
  const [isSpent, totalDeposits] = await Promise.all([
    mixerContract.methods.isSpent(depositObject.nullifierHex).call(),
    mixerContract.methods.nextIndex().call()
  ])
  const subsequentDeposits = Number(totalDeposits) - (Number(depositEvent.leafIndex) + 1) // leafIndex start with 0
  const timeSinceDeposit = commonUtils.ssToHHMMSS(commonUtils.getCurrentTimeInSeconds() - depositEvent.timestamp)

  return {mixer, isSpent, subsequentDeposits, timeSinceDeposit}
}

// ******************** stats section ******************** //

export async function getProtocolFee(web3, mixer) {
  const chainId = await web3.eth.net.getId()
  const mixerContract = new web3.eth.Contract(ABI['Mixer'], ADDRESS[chainId]['Mixer'][mixer['token']][mixer['denomination']])
  const [protocolFee, protocolFeeBase] = await Promise.all([
    mixerContract.methods.protocolFee().call(),
    mixerContract.methods.protocolFeeBase().call(),
  ])

  return protocolFee / protocolFeeBase
}

export async function getTotalDeposits(web3, mixer) {
  const chainId = await web3.eth.net.getId()
  const mixerContract = new web3.eth.Contract(ABI['Mixer'], ADDRESS[chainId]['Mixer'][mixer['token']][mixer['denomination']])
  const totalDeposits = await mixerContract.methods.nextIndex().call()

  return totalDeposits
}

export async function getLatestDepositEvents(web3, mixer, maxNumberOfEvents, cachedDepositEventsForAllMixers) {
  const [chainId, latestBlockNumber] = await Promise.all([ web3.eth.net.getId(), web3.eth.getBlockNumber()])
  const mixerContract = new web3.eth.Contract(ABI['Mixer'], ADDRESS[chainId]['Mixer'][mixer['token']][mixer['denomination']])
  // get all deposit events for this mixer
  const cachedDepositEvents = cachedDepositEventsForAllMixers[chainId][mixer['token']][mixer['denomination']]['events']
  const cachedUntilBlockNumber = cachedDepositEventsForAllMixers[chainId][mixer['token']][mixer['denomination']]['toBlock']
  const newDepositEvents = await contractUtils.getDepositEvents(web3, chainId, mixer, cachedUntilBlockNumber + 1, latestBlockNumber)
  const depositEvents = cachedDepositEvents.concat(newDepositEvents)
  const latestDepositEvents = depositEvents.slice(-maxNumberOfEvents)

  return latestDepositEvents.reverse()
}
