<template>
  <div class="mix">
    <div class="mix-l">
      <a-radio-group v-model="section" button-style="solid">
        <a-radio-button value="deposit"> Deposit </a-radio-button>
        <a-radio-button value="withdraw"> Withdraw </a-radio-button>
      </a-radio-group>
      <keep-alive>
        <Deposit v-if="section === 'deposit'" @mixerChanged="handleMixerChanged"/>
        <Withdraw v-else />
      </keep-alive>

    </div>
    <div class="mix-r">
      <div class="data-title">
        <div class="text">Statistics</div>
        <div class="tag" v-if="mixer">{{mixer['token']}} {{mixer['denomination']}}</div>
      </div>
      <div class="mix-r-inner">
        <div class="deposit-data">
          <ul class="data-group-1">
            <li>
              <div class="title">
                Anonymity Set
                <a-tooltip title="Number of deposits your withdrawal could originate from">
                  <img src="../assets/tips.svg" />
                </a-tooltip>
              </div>
              <div class="data"> {{ mixerStats? mixerStats['totalDeposits'] : '...' }} deposits</div>
            </li>
            <li>
              <div class="title">Mix Mining APR</div>
              <div class="data-big">{{ mixerStats? mixerStats['mixMiningAPR'] : '...' }} %</div>
            </li>
          </ul>
          <ul class="data-group-2">
            <div class="title">Latest Deposits</div>

            <div v-if="mixerStats">
              <li v-for="(depositEvent, index) in mixerStats['latestDepositEvents']" :key="index">
                <div class="name">{{ mixer['denomination'] | formatNumberInKilo(1000) }} {{ mixer['token'] }}</div>
                <div class="data">{{ depositEvent['timestamp'] | formatTimestampToTimeWindow }}</div>
              </li>
            </div>
            <div v-else>
              <li v-for="index in 5" :key="index">
                <div class="name">...</div>
              </li>
            </div>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "./Mix.scss";
import Deposit from "@/components/Deposit.vue";
import Withdraw from "@/components/Withdraw.vue";

import { mapState } from "vuex"
import * as contractUtils from '@/common/utils/contractUtils.js'
import ADDRESS from '@/common/address/address.js'
import CHAIN from '@/common/chain/chain.js'
import * as MixerCore from '@/common/core/mixer.js'
import * as VolMiningCore from '@/common/core/volMining.js'

export default {
  data() {
    return {
      section: "deposit",
      mixer: null,
      mixerStats: null,
      mixerStatsCache: {},
      numberOfLatestDepositsToShow: 5
    }
  },

  computed: {
    ...mapState({
      user: state => state.wallet.user,
      chainId: state => state.wallet.chainId,
      isWeb3Initialized: state => state.wallet.isWeb3Initialized,
      isDBInitialized: state => state.onionDB.isDBInitialized,
      cachedDepositEventsForAllMixers: state => state.onionDB.cachedDepositEventsForAllMixers
    }),

  },

  methods: {
    async handleMixerChanged(mixer) {
      console.log(mixer)
      this.mixer = mixer
      this.mixerStats = null
      this.mixerStats = await this.loadMixerStats()
    },

    async loadMixerStats() {
      if (this.isWeb3Initialized) {
        if (!this.mixerStatsCache[this.mixer['token']]) {
          this.mixerStatsCache[this.mixer['token']] = {}
        }
        if (!this.mixerStatsCache[this.mixer['token']][this.mixer['denomination']]) {
          const totalDeposits = await MixerCore.getTotalDeposits(window.web3, this.mixer)
          const mixMiningAPR = await VolMiningCore.getAPR(window.web3, this.mixer)
          const latestDepositEvents = await MixerCore.getLatestDepositEvents(window.web3, this.mixer, this.numberOfLatestDepositsToShow, this.cachedDepositEventsForAllMixers)
          // cache it for next use
          this.mixerStatsCache[this.mixer['token']][this.mixer['denomination']] = {totalDeposits, mixMiningAPR, latestDepositEvents}
        }
        return this.mixerStatsCache[this.mixer['token']][this.mixer['denomination']]
      } else {
        return null
      }
    }
  },

  components: {
    Deposit,
    Withdraw,
  }
}
</script>
