import tokenDecimals from '@/common/token/tokenDecimals'
import MIXER_CONFIG from '@/common/config/mixerConfig.js'
import { BigNumber } from 'bignumber.js'
import ADDRESS from '@/common/address/address.js'
import ABI from '@/common/abi/abi.js'
import CHAIN from '@/common/chain/chain.js'
const etherscanApi = require('etherscan-api')
const async_retry = require('async-retry')

const MAX_BLOCK_QUERY_RANGE = 4999
const RETRY_CONFIG = {
  retries: 2,
  factor: 3,
  minTimeout: 1 * 1000,
  maxTimeout: 6 * 1000,
  randomize: true,
}

export async function checkAndApproveForAllowance(tokenContract, owner, spender, allowance) {
  let currentAllowance = await tokenContract.methods.allowance(owner, spender).call()

  if (new BigNumber(currentAllowance).lt(new BigNumber(allowance))) {
    let maxAllowance = new BigNumber(2).pow(256).minus(1).toString(10)
    await tokenContract.methods.approve(spender, maxAllowance).send({from: owner})
  }
}


export async function hasAllowance(chainId, token, owner, spender, allowance) {
  console.log('hasAllowance')
  const tokenContract = new window.web3.eth.Contract(ABI['ERC20'], ADDRESS[chainId][token])
  const currentAllowance = await tokenContract.methods.allowance(owner, spender).call()

  return new BigNumber(currentAllowance).gte(new BigNumber(allowance))
}

export async function approve(chainId, token, owner, spender) {
  console.log('approve allowance')
  const tokenContract = new window.web3.eth.Contract(ABI['ERC20'], ADDRESS[chainId][token])
  const maxAllowance = new BigNumber(2).pow(256).minus(1).toString(10)
  await tokenContract.methods.approve(spender, maxAllowance).send({from: owner})
}


/*
* convert token value from ether to wei
*/
export function convertToBaseUnit(chainId, token, value) {
  let decimals = tokenDecimals[chainId][token]
  let valueInBaseUnit = new BigNumber(value).times(new BigNumber(10).pow(decimals)).toFixed(0) // no decimals

  return valueInBaseUnit
}

/*
* convert token value from wei to ether
*/
export function convertToMaxUnit(chainId, token, value) {
  let decimals = tokenDecimals[chainId][token]
  let valueInMaxUnit = new BigNumber(value).div(new BigNumber(10).pow(decimals)).toString(10) // keep every decimals

  return valueInMaxUnit
}

/**
* There are two way to fetch past events
* 1. use bscscan or etherscan api (return max 1000 events for one call): need to dedup because one block might have multiple
*    events, and there is a '1000' cap per call.
* 2. user getPastEvents method of web3 contract instance (max block range is 5000 for BSC)
* @commitment: used to filter for a specific deposit event if provided
**/
export async function getDepositEvents(web3, chainId, mixer, fromBlock, toBlock, commitment) {
  let events
  try {
    events = await _getDepositEventsWithEtherscan(web3, chainId, mixer, fromBlock, toBlock, commitment)
  } catch (e) {
    console.error(e)
    events = await _getDepositEventsWithWeb3(web3, chainId, mixer, fromBlock, toBlock, commitment)
  }

  return events
}

async function _getDepositEventsWithEtherscan(web3, chainId, mixer, fromBlock, toBlock, commitment) {
  console.log('_getDepositEventsWithEtherscan')
  const apiUrl = CHAIN[chainId]['scanApiUrl']
  if (!apiUrl) {
    throw new Error('This chain does not support etherscan api')
  }
  const apiKey = CHAIN[chainId]['scanApiKey']
  const timeout = 10000
  const api = etherscanApi.init(apiKey, apiUrl, timeout)
  const depositEventSignature = 'Deposit(bytes32,uint32,uint256)'
  const depositEventSignatureHash = web3.utils.soliditySha3(depositEventSignature)
  const mixerAddress = ADDRESS[chainId]['Mixer'][mixer['token']][mixer['denomination']]

  fromBlock =  Number(fromBlock)
  toBlock = Number(toBlock)
  let currentBlock = fromBlock
  let events = []
  let eventBatch = []
  // fetch all events, might contain dups
  do {
      eventBatch = await async_retry(
        async bail => {
          if (commitment) {
            return await api.log.getLogs(mixerAddress, currentBlock, toBlock, depositEventSignatureHash, 'and', commitment)
          } else {
            return await api.log.getLogs(mixerAddress, currentBlock, toBlock, depositEventSignatureHash)
          }
        },
        RETRY_CONFIG
      )
      // break if no events returned
      if (!eventBatch || !eventBatch.result || eventBatch.result.length == 0) {
        break;
      }
      events = events.concat(eventBatch.result)
      const lastEventBlockNumberHex = eventBatch.result[eventBatch.result.length - 1].blockNumber
      currentBlock = parseInt(lastEventBlockNumberHex, 16)
  } while (eventBatch.result.length >= 1000)  // current etherscan max result is 1000, here we use >= instead of == in case they increase the limit in the future

  // deserialize
  events = events.map(e => {
    const eventData = e.data.substring(2) // remove 0x
    const leafIndex = eventData.substr(0, 64) // first 32 bytes
    const timestamp = eventData.substr(64, 128) // second 32 bytes
    return {
      'commitment': e.topics[1],
      'leafIndex': String(parseInt(leafIndex, 16)),
      'timestamp': String(parseInt(timestamp, 16))
    }
  })

  // sort
  events = events.sort((a, b) => a.leafIndex - b.leafIndex)

  // de-dup
  events = events.filter((event, index, self) => self.findIndex(t => t.commitment === event.commitment) === index)

  return events
}

async function _getDepositEventsWithWeb3(web3, chainId, mixer, fromBlock, toBlock, commitment) {
  console.log('_getDepositEventsWithWeb3')
  const mixerContract = new web3.eth.Contract(ABI['Mixer'], ADDRESS[chainId]['Mixer'][mixer['token']][mixer['denomination']])
  fromBlock =  Number(fromBlock)
  toBlock = Number(toBlock)
  let currentBlock = fromBlock
  let events = []
  let eventBatch = []
  // fetch all events
  while (currentBlock <= toBlock) {
    eventBatch = await async_retry(
      async bail => {
        if (commitment) {
          return await mixerContract.getPastEvents('Deposit', {filter: {commitment}, fromBlock: currentBlock, toBlock: Math.min(currentBlock + MAX_BLOCK_QUERY_RANGE, toBlock)})
        } else {
          return await mixerContract.getPastEvents('Deposit', {fromBlock: currentBlock, toBlock: Math.min(currentBlock + MAX_BLOCK_QUERY_RANGE, toBlock)})
        }
      },
      RETRY_CONFIG
    )
    // only concate if there is element
    if (eventBatch && eventBatch.length > 0) {
      events = events.concat(eventBatch)
    }
    events = events.concat(eventBatch)
    currentBlock = currentBlock + MAX_BLOCK_QUERY_RANGE + 1
  }
  // deserialize
  events = events.map(e => {
    return {
      'commitment': e.returnValues.commitment,
      'leafIndex': e.returnValues.leafIndex,
      'timestamp': e.returnValues.timestamp
    }
  })

  // sort
  events = events.sort((a, b) => a.leafIndex - b.leafIndex)

  return events
}

// put native token first in array
export function getSupportedMixerTokens(chainId) {
  let supportedMixerTokens = []
  for (const token in MIXER_CONFIG[chainId]) {
    supportedMixerTokens.push(token)
  }
  supportedMixerTokens.sort((t1, t2) => {
    if (isNativeToken(t1)) {
      return -1
    } else if (isNativeToken(t2)) {
      return 1
    } else {
      return 0
    }
  })
  return supportedMixerTokens
}

// sort by denomination
export function getSupportedMixerDenominations(chainId, token) {
  let supportedMixerDenominations = []
  for (const denomination in MIXER_CONFIG[chainId][token]) {
    supportedMixerDenominations.push(denomination)
  }
  supportedMixerDenominations.sort((d1, d2) => {
    d1 = Number(d1)
    d2 = Number(d2)
    if (d1 < d2) {
      return -1
    } else if (d1 > d2) {
      return 1
    } else {
      return 0
    }
  })
  return supportedMixerDenominations
}

export function getOurToken() {
  return 'OMT'
}

export function isNativeToken(token) {
  let nativeTokens = ['BNB', 'ETH', 'HECO']
  return nativeTokens.includes(token)
}

export async function sleep(timeMs) {
  await new Promise(resolve => setTimeout(resolve, timeMs))
}
