import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import i18n from '@/common/lang/i18n.js'
import { BigNumber } from 'bignumber.js'
import { Loading } from 'element-ui'
import Antd from 'ant-design-vue'
import Clipboard from 'v-clipboard'
import "ant-design-vue/lib/dropdown/style/index.css"
import "ant-design-vue/lib/radio/style/index.css"
import "ant-design-vue/lib/select/style/index.css"
import "ant-design-vue/lib/menu/style/index.css"
import "ant-design-vue/lib/input/style/index.css"
import "ant-design-vue/lib/checkbox/style/index.css"
import "ant-design-vue/lib/modal/style/index.css"
import "ant-design-vue/lib/button/style/index.css"
import "ant-design-vue/lib/spin/style/index.css"
import "ant-design-vue/lib/icon/style/index.css"
import "ant-design-vue/lib/tooltip/style/index.css"
import "ant-design-vue/lib/notification/style/index.css"

Vue.config.productionTip = false
Vue.use(Antd)
Vue.use(Loading)
Vue.use(Clipboard)

// filters
Vue.filter('formatNumber', function (number, decimals) {
  if (number === undefined || number == null) {
    return '--'
  } else {
    return new BigNumber(number).toFixed(decimals, 1)
  }
})

Vue.filter('formatPercent', function (number, decimals) {
  if (number === undefined || number == null) {
    return '--'
  } else {
    return new BigNumber(number).times(100).toFixed(decimals, 1)
  }
})

Vue.filter('formatNumberInKilo', function (number, threshHold) {
  if (number === undefined || number == null) {
    return '--'
  } else if (new BigNumber(number).lt(threshHold)) {
    return number
  } else {
    number = new BigNumber(number).div(1000).toString(10)
    return `${number}K`
  }
})

Vue.filter('shortenAddress', function (address) {
  if (address === undefined || address == null) {
    return '--'
  } else {
    const prefix = address.substring(0, 6)
    const suffix = address.substring(address.length - 4, address.length)
    return `${prefix}...${suffix}`
  }
})

Vue.filter('formatTimestampToTimeWindow', function (timestampInSecond) {
  if (timestampInSecond === undefined || timestampInSecond == null) {
    return '--'
  } else {
    const currentTimeInMs= new Date().getTime()
    const currentTimeInSeconds = (currentTimeInMs - (currentTimeInMs % 1000)) / 1000
    let duration = currentTimeInSeconds - timestampInSecond
    const secs = duration % 60
    duration = (duration - secs) / 60
    const mins = duration % 60
    const hrs = (duration - mins) / 60
    if (hrs >= 24 * 7) {
      return 'a week ago'
    } else if (hrs >= 48) {
      return `${Math.floor(hrs/24)} days ago`
    } else if (hrs >= 24) {
      return '1 day ago'
    } else if (hrs > 1) {
      return `${hrs} hours ago`
    } else if (hrs == 1) {
      return `1 hours ago`
    } else if (mins > 1) {
      return `${mins} minutes ago`
    } else if (mins == 1) {
      return `1 minute ago`
    } else if (secs > 1) {
      return `${secs} seconds ago`
    } else if (secs == 1) {
      return '1 second ago'
    }
  }
})

new Vue({
  router,
  i18n,
  store,
  render: h => h(App)
}).$mount('#app')
