import Vue from 'vue'
import VueRouter from 'vue-router'
import Mix from '../views/Mix.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Mix',
    component: Mix
  },
  {
    path: '/mining',
    name: 'Mining',
    component: () => import('../views/Mining.vue')
  },
  {
    path: '/vault',
    name: 'Vault',
    component: () => import('../views/Vault.vue')
  }
]

const router = new VueRouter({
  //mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
